.contacto{
    text-align: center;
    background-image: url(/build/img/encuentra.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    color: $blanco;
    h2{
        padding: 2rem;
        text-transform: uppercase;
        font-size: 4em;
        @include tablet{
            font-size: 3.5em;
        }
        @include telefono{
            font-size: 2.5em;
        }   
    }
}
.contacto_body{
    height: 40vh;
    padding: 0 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include tablet{
        height: 30rem;
    }
    h3{
       font-size: 3.5em;
       @include tablet{
          font-size: 2.5em;
       }
       @include telefono{
           font-size: 2.5em;
       }
    }
    p{
      font-size: 2.5em;
      @include telefono{
        font-size: 1.8em;
      }
    }
}
.btn-contacto{ 
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem;
    a{
        @include boton(flex,center,20% ,2rem);
        color: $blanco;
        background-color: $cafe;
        font-size: 2.5em;
        @include tablet{
            width: 60%;
            font-size: 2.5em;
        }
        @include telefono{
            width: 100%;
            font-size: 2em;
        }
        
    }
    
}